import {MenuLevel} from './MenuLevel';
import {LevelLink} from './LevelLink';

export class MenuTree {
  private levels: MenuLevel[];

  constructor() {
    let testItems = [
      new MenuLevel(
        [
          new LevelLink('Главная', '/', -1),
          new LevelLink('Залы', null, 1),
          new LevelLink('Платья', 'dresses', -1),
          // new LevelLink('Back Stage', 'backstage', -1),
          new LevelLink('Контакты', 'contacts', -1),
          new LevelLink('Правила аренды', 'rules', -1),
          new LevelLink('Услуги',null,4),
          new LevelLink('Оборудование', 'equipment', -1),
          new LevelLink('Портфолио', 'portfolio', -1),
          new LevelLink('Календарь', 'calendar', -1),
        ],
        -1, 0),
        new MenuLevel(
          [
            new LevelLink('День Рождения под ключ', 'den-rozhdenija', -1),
            // new LevelLink('Мастер Классы', 'master-klassy', -1),
          ],
          0, 4),

      new MenuLevel(
        [
          new LevelLink('Open Space', 'open-space', -1),
          new LevelLink('Fashion Space', 'fashion-space', -1),
          new LevelLink('Loft Space', 'loft-space', -1),
          new LevelLink('White Space', 'white-space', -1),
          new LevelLink('Hygge Space', 'hygge-space', -1),
          new LevelLink('Make Up & Dress room', 'make-up-dress-room', -1)

        ],
        0, 1),
      new MenuLevel(
        [
          new LevelLink('Женские', '/dresses/woman', -1),
          new LevelLink('Детские', '/dresses/kids', -1),
        ], 0, 2),
    ];
    this.setLevels(testItems);
  }

  getLevels = (): MenuLevel[] => {
    return this.levels;
  }

  setLevels = (levels: MenuLevel[]): void => {
    this.levels = levels;
  }

  addLevel = (level: MenuLevel): void => {
    this.levels.push(level);
  }


}
